.DrawerToggle {
 width: 40px;
 height: 100%;
 display: -ms-flexbox;
 display: flex;
 -ms-flex-flow: column;
     flex-flow: column;
 -ms-flex-pack: distribute;
     justify-content: space-around;
 -ms-flex-align: center;
     align-items: center;
 padding: 50px 0;
 -webkit-box-sizing: border-box;
         box-sizing: border-box;
 cursor: pointer;
}

.DrawerToggle div {
 width: 90%;
 height: 2px;
 background-color: black;
}

@media (min-width: 500px) {
 .DrawerToggle {
     display: none;
 }
}


