.Card{
    width: 250px;
    padding: 26px;
    text-align: center;
    border: 1px solid #eee;
    -webkit-box-shadow: 0 2px 3px #ccc;
            box-shadow: 0 2px 3px #ccc;
    margin: 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 20px;
    cursor: pointer;
    background-color: #1494aa;
    display: inline-block;
}



